<template>
    <div class="sh-form-item">
        <label class="sh-form-item__label" :style="{width: this.Form.labelWidth}">
            {{ label }}
        </label>
        <div class="sh-form-item__content">
            <slot></slot>
        </div>
    </div>
</template>

<script>

export default {
  name: 'ShFormItem',
  props:{
      label: String
  },
  inject: ['Form'],
  methods: {
  }
}
</script>

<style lang="scss" scoped>
.sh-form--inline .sh-form-item,.sh-form--inline .sh-form-item__content {
	display:inline-block;
	vertical-align:top
}
.sh-form-item::after,.sh-form-item__content::after {
	clear:both
}
.sh-form--label-left .sh-form-item__label {
	text-align:left
}
.sh-form--label-top .sh-form-item__label {
	float:none;
	display:inline-block;
	text-align:left;
	padding:0 0 10px
}
.sh-form--inline .sh-form-item {
	margin-right:10px
}
.sh-form--inline .sh-form-item__label {
	float:none;
	display:inline-block
}
.sh-form--inline.sh-form--label-top .sh-form-item__content {
	display:block
}
.sh-form-item {
	margin-bottom:22px
}
.sh-form-item::after,.sh-form-item::before {
	display:table;
	content:""
}
.sh-form-item .sh-form-item {
	margin-bottom:0
}
.sh-form-item--mini.sh-form-item,.sh-form-item--small.sh-form-item {
	margin-bottom:18px
}
.sh-form-item .sh-input__validateIcon {
	display:none
}
.sh-form-item--medium .sh-form-item__content,.sh-form-item--medium .sh-form-item__label {
	line-height:36px
}
.sh-form-item--small .sh-form-item__content,.sh-form-item--small .sh-form-item__label {
	line-height:32px
}
.sh-form-item--small .sh-form-item__error {
	padding-top:2px
}
.sh-form-item--mini .sh-form-item__content,.sh-form-item--mini .sh-form-item__label {
	line-height:28px
}
.sh-form-item--mini .sh-form-item__error {
	padding-top:1px
}
.sh-form-item__label-wrap {
	float:left
}
.sh-form-item__label-wrap .sh-form-item__label {
	display:inline-block;
	float:none
}
.sh-form-item__label {
	text-align:right;
	vertical-align:middle;
	float:left;
	font-size:14px;
	color:#606266;
	line-height:40px;
	padding:0 12px 0 0;
	-webkit-box-sizing:border-box;
	box-sizing:border-box
}
.sh-form-item__content {
	line-height:40px;
	position:relative;
	font-size:14px
}
.sh-form-item__content::after,.sh-form-item__content::before {
	display:table;
	content:""
}
.sh-form-item__content .sh-input-group {
	vertical-align:top
}
.sh-form-item__error {
	color:#F56C6C;
	font-size:12px;
	line-height:1;
	padding-top:4px;
	position:absolute;
	top:100%;
	left:0
}
.sh-form-item__error--inline {
	position:relative;
	top:auto;
	left:auto;
	display:inline-block;
	margin-left:10px
}
.sh-form-item.is-required:not(.is-no-asterisk) .sh-form-item__label-wrap>.sh-form-item__label:before,.sh-form-item.is-required:not(.is-no-asterisk)>.sh-form-item__label:before {
	content:'*';
	color:#F56C6C;
	margin-right:4px
}
.sh-form-item.is-error .sh-input__inner,.sh-form-item.is-error .sh-input__inner:focus,.sh-form-item.is-error .sh-textarea__inner,.sh-form-item.is-error .sh-textarea__inner:focus {
	border-color:#F56C6C
}
.sh-form-item.is-error .sh-input-group__append .sh-input__inner,.sh-form-item.is-error .sh-input-group__prepend .sh-input__inner {
	border-color:transparent
}
.sh-form-item.is-error .sh-input__validateIcon {
	color:#F56C6C
}
.sh-form-item--feedback .sh-input__validateIcon {
	display:inline-block
}
</style>



