<template>
  <label
    class="sh-checkbox"
    :class="[
      border && checkboxSize ? 'sh-checkbox--' + checkboxSize : '',
      { 'is-disabled': isDisabled },
      { 'is-bordered': border },
      { 'is-checked': isChecked }
    ]"
    :id="id"
  >
    <span class="sh-checkbox__input"
      :class="{
        'is-disabled': isDisabled,
        'is-checked': isChecked,
        'is-indeterminate': indeterminate,
        'is-focus': focus
      }"
      :tabindex="indeterminate ? 0 : false"
      :role="indeterminate ? 'checkbox' : false"
      :aria-checked="indeterminate ? 'mixed' : false"
    >
      <span class="sh-checkbox__inner"></span>
      <input
        v-if="trueLabel || falseLabel"
        class="sh-checkbox__original"
        type="checkbox"
        :aria-hidden="indeterminate ? 'true' : 'false'"
        :name="name"
        :disabled="isDisabled"
        :true-value="trueLabel"
        :false-value="falseLabel"
        v-model="model"
        @change="handleChange"
        @focus="focus = true"
        @blur="focus = false">
      <input
        v-else
        class="sh-checkbox__original"
        type="checkbox"
        :aria-hidden="indeterminate ? 'true' : 'false'"
        :disabled="isDisabled"
        :value="label"
        :name="name"
        v-model="model"
        @change="handleChange"
        @focus="focus = true"
        @blur="focus = false">
    </span>
    <span class="sh-checkbox__label" v-if="$slots.default || label">
      <slot></slot>
      <template v-if="!$slots.default">{{label}}</template>
    </span>
  </label>
</template>
<script>
  import Emitter from './js/utils/emitter';

  export default {
    name: 'ShCheckbox',

    mixins: [Emitter],

    inject: {
      elForm: {
        default: ''
      },
      elFormItem: {
        default: ''
      }
    },

    componentName: 'ShCheckbox',

    data() {
      return {
        selfModel: false,
        focus: false,
        isLimitExceeded: false
      };
    },

    computed: {
      model: {
        get() {
          return this.isGroup
            ? this.store : this.value !== undefined
              ? this.value : this.selfModel;
        },

        set(val) {
          if (this.isGroup) {
            this.isLimitExceeded = false;
            (this._checkboxGroup.min !== undefined &&
              val.length < this._checkboxGroup.min &&
              (this.isLimitExceeded = true));

            (this._checkboxGroup.max !== undefined &&
              val.length > this._checkboxGroup.max &&
              (this.isLimitExceeded = true));

            this.isLimitExceeded === false &&
            this.dispatch('ShCheckboxGroup', 'input', [val]);
          } else {
            this.$emit('input', val);
            this.selfModel = val;
          }
        }
      },

      isChecked() {
        if ({}.toString.call(this.model) === '[object Boolean]') {
          return this.model;
        } else if (Array.isArray(this.model)) {
          return this.model.indexOf(this.label) > -1;
        } else if (this.model !== null && this.model !== undefined) {
          return this.model === this.trueLabel;
        }
      },

      isGroup() {
        let parent = this.$parent;
        while (parent) {
          if (parent.$options.componentName !== 'ShCheckboxGroup') {
            parent = parent.$parent;
          } else {
            this._checkboxGroup = parent;
            return true;
          }
        }
        return false;
      },

      store() {
        return this._checkboxGroup ? this._checkboxGroup.value : this.value;
      },

      /* used to make the isDisabled judgment under max/min props */
      isLimitDisabled() {
        const { max, min } = this._checkboxGroup;
        return !!(max || min) &&
          (this.model.length >= max && !this.isChecked) ||
          (this.model.length <= min && this.isChecked);
      },

      isDisabled() {
        return this.isGroup
          ? this._checkboxGroup.disabled || this.disabled || (this.elForm || {}).disabled || this.isLimitDisabled
          : this.disabled || (this.elForm || {}).disabled;
      },

      _elFormItemSize() {
        return (this.elFormItem || {}).elFormItemSize;
      },

      checkboxSize() {
        const temCheckboxSize = this.size || this._elFormItemSize || (this.$ELEMENT || {}).size;
        return this.isGroup
          ? this._checkboxGroup.checkboxGroupSize || temCheckboxSize
          : temCheckboxSize;
      }
    },

    props: {
      value: {},
      label: {},
      indeterminate: Boolean,
      disabled: Boolean,
      checked: Boolean,
      name: String,
      trueLabel: [String, Number],
      falseLabel: [String, Number],
      id: String, /* 当indeterminate为真时，为controls提供相关连的checkbox的id，表明元素间的控制关系*/
      controls: String, /* 当indeterminate为真时，为controls提供相关连的checkbox的id，表明元素间的控制关系*/
      border: Boolean,
      size: String
    },

    methods: {
      addToStore() {
        if (
          Array.isArray(this.model) &&
          this.model.indexOf(this.label) === -1
        ) {
          this.model.push(this.label);
        } else {
          this.model = this.trueLabel || true;
        }
      },
      handleChange(ev) {
        if (this.isLimitExceeded) return;
        let value;
        if (ev.target.checked) {
          value = this.trueLabel === undefined ? true : this.trueLabel;
        } else {
          value = this.falseLabel === undefined ? false : this.falseLabel;
        }
        this.$emit('change', value, ev);
        this.$nextTick(() => {
          if (this.isGroup) {
            this.dispatch('ShCheckboxGroup', 'change', [this._checkboxGroup.value]);
          }
        });
      }
    },

    created() {
      this.checked && this.addToStore();
    },
    mounted() { // 为indeterminate元素 添加aria-controls 属性
      if (this.indeterminate) {
        this.$el.setAttribute('aria-controls', this.controls);
      }
    },

    watch: {
      value(value) {
        this.dispatch('ElFormItem', 'el.form.change', value);
      }
    }
  };
</script>

<style lang="scss" scoped>
@charset "UTF-8";
.sh-checkbox,.sh-checkbox__input {
	display:inline-block;
	position:relative
}
.sh-checkbox-button__inner,.sh-checkbox__input {
	white-space:nowrap;
	vertical-align:middle;
	outline:0
}
.sh-checkbox {
	color:#606266;
	font-weight:500;
	font-size:14px;
	cursor:pointer;
	white-space:nowrap;
	-webkit-user-select:none;
	-moz-user-select:none;
	-ms-user-select:none;
	user-select:none;
	margin-right:30px
}
.sh-checkbox.is-bordered {
	padding:9px 20px 9px 10px;
	border-radius:4px;
	border:1px solid #DCDFE6;
	-webkit-box-sizing:border-box;
	box-sizing:border-box;
	line-height:normal;
	height:40px
}
.sh-checkbox.is-bordered.is-checked {
	border-color:#409EFF
}
.sh-checkbox.is-bordered.is-disabled {
	border-color:#EBEEF5;
	cursor:not-allowed
}
.sh-checkbox.is-bordered+.sh-checkbox.is-bordered {
	margin-left:10px
}
.sh-checkbox.is-bordered.sh-checkbox--medium {
	padding:7px 20px 7px 10px;
	border-radius:4px;
	height:36px
}
.sh-checkbox.is-bordered.sh-checkbox--medium .sh-checkbox__label {
	line-height:17px;
	font-size:14px
}
.sh-checkbox.is-bordered.sh-checkbox--medium .sh-checkbox__inner {
	height:14px;
	width:14px
}
.sh-checkbox.is-bordered.sh-checkbox--small {
	padding:5px 15px 5px 10px;
	border-radius:3px;
	height:32px
}
.sh-checkbox.is-bordered.sh-checkbox--small .sh-checkbox__label {
	line-height:15px;
	font-size:12px
}
.sh-checkbox.is-bordered.sh-checkbox--small .sh-checkbox__inner {
	height:12px;
	width:12px
}
.sh-checkbox.is-bordered.sh-checkbox--small .sh-checkbox__inner::after {
	height:6px;
	width:2px
}
.sh-checkbox.is-bordered.sh-checkbox--mini {
	padding:3px 15px 3px 10px;
	border-radius:3px;
	height:28px
}
.sh-checkbox.is-bordered.sh-checkbox--mini .sh-checkbox__label {
	line-height:12px;
	font-size:12px
}
.sh-checkbox.is-bordered.sh-checkbox--mini .sh-checkbox__inner {
	height:12px;
	width:12px
}
.sh-checkbox.is-bordered.sh-checkbox--mini .sh-checkbox__inner::after {
	height:6px;
	width:2px
}
.sh-checkbox__input {
	cursor:pointer;
	line-height:1
}
.sh-checkbox__input.is-disabled .sh-checkbox__inner {
	background-color:#edf2fc;
	border-color:#DCDFE6;
	cursor:not-allowed
}
.sh-checkbox__input.is-disabled .sh-checkbox__inner::after {
	cursor:not-allowed;
	border-color:#C0C4CC
}
.sh-checkbox__input.is-disabled .sh-checkbox__inner+.sh-checkbox__label {
	cursor:not-allowed
}
.sh-checkbox__input.is-disabled.is-checked .sh-checkbox__inner {
	background-color:#F2F6FC;
	border-color:#DCDFE6
}
.sh-checkbox__input.is-disabled.is-checked .sh-checkbox__inner::after {
	border-color:#C0C4CC
}
.sh-checkbox__input.is-disabled.is-indeterminate .sh-checkbox__inner {
	background-color:#F2F6FC;
	border-color:#DCDFE6
}
.sh-checkbox__input.is-disabled.is-indeterminate .sh-checkbox__inner::before {
	background-color:#C0C4CC;
	border-color:#C0C4CC
}
.sh-checkbox__input.is-checked .sh-checkbox__inner,.sh-checkbox__input.is-indeterminate .sh-checkbox__inner {
	background-color:#409EFF;
	border-color:#409EFF
}
.sh-checkbox__input.is-disabled+span.sh-checkbox__label {
	color:#C0C4CC;
	cursor:not-allowed
}
.sh-checkbox__input.is-checked .sh-checkbox__inner::after {
	-webkit-transform:rotate(45deg) scaleY(1);
	transform:rotate(45deg) scaleY(1)
}
.sh-checkbox__input.is-checked+.sh-checkbox__label {
	color:#409EFF
}
.sh-checkbox__input.is-focus .sh-checkbox__inner {
	border-color:#409EFF
}
.sh-checkbox__input.is-indeterminate .sh-checkbox__inner::before {
	content:'';
	position:absolute;
	display:block;
	background-color:#FFF;
	height:2px;
	-webkit-transform:scale(.5);
	transform:scale(.5);
	left:0;
	right:0;
	top:5px
}
.sh-checkbox__input.is-indeterminate .sh-checkbox__inner::after {
	display:none
}
.sh-checkbox__inner {
	display:inline-block;
	position:relative;
	border:1px solid #DCDFE6;
	border-radius:2px;
	-webkit-box-sizing:border-box;
	box-sizing:border-box;
	width:14px;
	height:14px;
	background-color:#FFF;
	z-index:1;
	-webkit-transition:border-color .25s cubic-bezier(.71,-.46,.29,1.46),background-color .25s cubic-bezier(.71,-.46,.29,1.46);
	transition:border-color .25s cubic-bezier(.71,-.46,.29,1.46),background-color .25s cubic-bezier(.71,-.46,.29,1.46)
}
.sh-checkbox__inner:hover {
	border-color:#409EFF
}
.sh-checkbox__inner::after {
	-webkit-box-sizing:content-box;
	box-sizing:content-box;
	content:"";
	border:1px solid #FFF;
	border-left:0;
	border-top:0;
	height:7px;
	left:4px;
	position:absolute;
	top:1px;
	-webkit-transform:rotate(45deg) scaleY(0);
	transform:rotate(45deg) scaleY(0);
	width:3px;
	-webkit-transition:-webkit-transform .15s ease-in .05s;
	transition:-webkit-transform .15s ease-in .05s;
	transition:transform .15s ease-in .05s;
	transition:transform .15s ease-in .05s,-webkit-transform .15s ease-in .05s;
	-webkit-transform-origin:center;
	transform-origin:center
}
.sh-checkbox__original {
	opacity:0;
	outline:0;
	position:absolute;
	margin:0;
	width:0;
	height:0;
	z-index:-1
}
</style>


