<template>
  <a
    :class="[
      'sh-link',
      type ? `sh-link--${type}` : '',
      disabled && 'is-disabled',
      underline && !disabled && 'is-underline'
    ]"
    :href="disabled ? null : href"
    v-bind="$attrs"
    @click="handleClick"
  >

    <i :class="icon" v-if="icon"></i>

    <span v-if="$slots.default" class="sh-link--inner">
      <slot></slot>
    </span>

    <template v-if="$slots.icon"><slot v-if="$slots.icon" name="icon"></slot></template>
  </a>
</template>

<script>

export default {
  name: 'ShLink',

  props: {
    type: {
      type: String,
      default: 'default'
    },
    underline: {
      type: Boolean,
      default: true
    },
    disabled: Boolean,
    href: String,
    icon: String
  },

  methods: {
    handleClick(event) {
      if (!this.disabled) {
        if (!this.href) {
          this.$emit('click', event);
        }
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.sh-link {
	display:-webkit-inline-box;
	display:-ms-inline-flexbox;
	display:inline-flex;
	-webkit-box-orient:horizontal;
	-webkit-box-direction:normal;
	-ms-flex-direction:row;
	flex-direction:row;
	-webkit-box-align:center;
	-ms-flex-align:center;
	align-items:center;
	-webkit-box-pack:center;
	-ms-flex-pack:center;
	justify-content:center;
	vertical-align:middle;
	position:relative;
	text-decoration:none;
	outline:0;
	cursor:pointer;
	padding:0;
	font-size:14px;
	font-weight:500
}
.sh-link.is-underline:hover:after {
	content:"";
	position:absolute;
	left:0;
	right:0;
	height:0;
	bottom:0;
	border-bottom:1px solid #409EFF
}
.sh-link.sh-link--default:after,.sh-link.sh-link--primary.is-underline:hover:after,.sh-link.sh-link--primary:after {
	border-color:#409EFF
}
.sh-link.is-disabled {
	cursor:not-allowed
}
.sh-link [class*=sh-icon-]+span {
	margin-left:5px
}
.sh-link.sh-link--default {
	color:#606266
}
.sh-link.sh-link--default:hover {
	color:#409EFF
}
.sh-link.sh-link--default.is-disabled {
	color:#C0C4CC
}
.sh-link.sh-link--primary {
	color:#409EFF
}
.sh-link.sh-link--primary:hover {
	color:#66b1ff
}
.sh-link.sh-link--primary.is-disabled {
	color:#a0cfff
}
.sh-link.sh-link--danger.is-underline:hover:after,.sh-link.sh-link--danger:after {
	border-color:#F56C6C
}
.sh-link.sh-link--danger {
	color:#F56C6C
}
.sh-link.sh-link--danger:hover {
	color:#f78989
}
.sh-link.sh-link--danger.is-disabled {
	color:#fab6b6
}
.sh-link.sh-link--success.is-underline:hover:after,.sh-link.sh-link--success:after {
	border-color:#67C23A
}
.sh-link.sh-link--success {
	color:#67C23A
}
.sh-link.sh-link--success:hover {
	color:#85ce61
}
.sh-link.sh-link--success.is-disabled {
	color:#b3e19d
}
.sh-link.sh-link--warning.is-underline:hover:after,.sh-link.sh-link--warning:after {
	border-color:#E6A23C
}
.sh-link.sh-link--warning {
	color:#E6A23C
}
.sh-link.sh-link--warning:hover {
	color:#ebb563
}
.sh-link.sh-link--warning.is-disabled {
	color:#f3d19e
}
.sh-link.sh-link--info.is-underline:hover:after,.sh-link.sh-link--info:after {
	border-color:#909399
}
.sh-link.sh-link--info {
	color:#909399
}
.sh-link.sh-link--info:hover {
	color:#a6a9ad
}
.sh-link.sh-link--info.is-disabled {
	color:#c8c9cc
}
</style>



