<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Form 表单</h2>
            <p slot="title" >由输入框、选择器、单选框、多选框等控件组成，用以收集、提交数据。</p>
        </Viwe>
        <Viwe>
            <h3 slot="name" >基础用法</h3>
            <p slot="title" >包括各种表单项，比如输入框、开关、单选框、多选框等。</p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <sh-form ref="form" :model="form" label-width="80px">
                            <sh-form-item label="活动名称">
                                <sh-input v-model="form.name"></sh-input>
                            </sh-form-item>
                            <sh-form-item label="即时配送">
                                <sh-switch v-model="form.delivery"></sh-switch>
                            </sh-form-item>
                            <sh-form-item label="活动性质">
                                <sh-checkbox-group v-model="form.type">
                                <sh-checkbox label="美食/餐厅线上活动" name="type"></sh-checkbox>
                                <sh-checkbox label="地推活动" name="type"></sh-checkbox>
                                <sh-checkbox label="线下主题活动" name="type"></sh-checkbox>
                                <sh-checkbox label="单纯品牌曝光" name="type"></sh-checkbox>
                                </sh-checkbox-group>
                            </sh-form-item>
                            <sh-form-item label="特殊资源">
                                <sh-radio-group v-model="form.resource">
                                <sh-radio label="线上品牌商赞助"></sh-radio>
                                <sh-radio label="线下场地免费"></sh-radio>
                                </sh-radio-group>
                            </sh-form-item>
                            <sh-form-item>
                                <sh-button type="primary" @click="onSubmit">立即创建</sh-button>
                                <sh-button>取消</sh-button>
                            </sh-form-item>
                        </sh-form>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;sh-form ref=&quot;form&quot; :model=&quot;form&quot; label-width=&quot;80px&quot;&gt;
  &lt;sh-form-item label=&quot;活动名称&quot;&gt;
    &lt;sh-input v-model=&quot;form.name&quot;&gt;&lt;/sh-input&gt;
  &lt;/sh-form-item&gt;
  &lt;sh-form-item label=&quot;即时配送&quot;&gt;
    &lt;sh-switch v-model=&quot;form.delivery&quot;&gt;&lt;/sh-switch&gt;
  &lt;/sh-form-item&gt;
  &lt;sh-form-item label=&quot;活动性质&quot;&gt;
    &lt;sh-checkbox-group v-model=&quot;form.type&quot;&gt;
      &lt;sh-checkbox label=&quot;美食/餐厅线上活动&quot; name=&quot;type&quot;&gt;&lt;/sh-checkbox&gt;
      &lt;sh-checkbox label=&quot;地推活动&quot; name=&quot;type&quot;&gt;&lt;/sh-checkbox&gt;
      &lt;sh-checkbox label=&quot;线下主题活动&quot; name=&quot;type&quot;&gt;&lt;/sh-checkbox&gt;
      &lt;sh-checkbox label=&quot;单纯品牌曝光&quot; name=&quot;type&quot;&gt;&lt;/sh-checkbox&gt;
    &lt;/sh-checkbox-group&gt;
  &lt;/sh-form-item&gt;
  &lt;sh-form-item label=&quot;特殊资源&quot;&gt;
    &lt;sh-radio-group v-model=&quot;form.resource&quot;&gt;
      &lt;sh-radio label=&quot;线上品牌商赞助&quot;&gt;&lt;/sh-radio&gt;
      &lt;sh-radio label=&quot;线下场地免费&quot;&gt;&lt;/sh-radio&gt;
    &lt;/sh-radio-group&gt;
  &lt;/sh-form-item&gt;
  &lt;sh-form-item&gt;
    &lt;sh-button type=&quot;primary&quot; @click=&quot;onSubmit&quot;&gt;立即创建&lt;/sh-button&gt;
    &lt;sh-button&gt;取消&lt;/sh-button&gt;
  &lt;/sh-form-item&gt;
&lt;/sh-form&gt;
&lt;script&gt;
  export default {
    data() {
      return {
        form: {
          name: &#x27;&#x27;,
          delivery: false,
          type: [],
          resource: &#x27;&#x27;
        }
      }
    },
    methods: {
      onSubmit() {
        console.log(&#x27;submit!&#x27;);
      }
    }
  }
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive" class="sh-icon-caret-top"></i>
                        <span @click="isActive = !isActive">{{ isActive?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'Form',
  data(){
    return {
        isActive: false,
        form: {
          name: '',
          delivery: false,
          type: [],
          resource: ''
        }
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
      onSubmit() {
        console.log('submit!');
      }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';

</style>



