<template>
    <div class="demo-viwe">
        <div class="demo-viwe-name" v-if="$slots.name">
            <slot name="name"></slot>
        </div>
        <p class="demo-viwe-title" v-if="$slots.title">
            <slot name="title"></slot>
        </p>
        <div class="demo-viwe-content" v-if="$slots.content">
            <slot name="content"></slot>
        </div>
        <div class="demo-viwe-explain" v-if="explain">
            <div class="demo-viwe-explain-attributes" v-for="(itme,index1) in explain" :key="index1">
                <h3>{{ itme.name }}</h3>
                <table>
                    <thead>
                        <tr>
                            <th v-for="(itme2,index2) in itme.title" :key="index2">{{ itme2 }}</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(itme3,index3) in itme.value" :key="index3">
                            <td v-for="(itme4,index4) in itme3" :key="index4">{{ itme4 }}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <slot></slot>
    </div>
</template>

<script>

export default {
  name: 'viwe',
  props:{
      title:{
          type: String,
          default: ''
      },
      name:{
          type: String,
          default: ''
      },
      explain:{
          type: Array
      }
  },
  data(){
    return {
        explain1: [
            {
                "name": "VIWE Attributes",
                "title":['参数','说明','类型','可选值','默认值'],
                "value":[
                    ['参数','说明','类型','可选值','默认值'],
                    ['参数','说明','类型','可选值','默认值'],
                    ['参数','说明','类型','可选值','默认值']
                ],
            }
        ]
    }
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
.demo-viwe{
    .demo-viwe-name{
        margin: 55px 0 20px;
        font-weight: 400;
        color: #1f2f3d;
    }
    .demo-viwe-title{
        font-size: 14px;
        color: #5e6d82;
        line-height: 1.5em;
    }
    .demo-viwe-content{

    }
    .demo-viwe-explain{
        .demo-viwe-explain-attributes{
            table{
                border-collapse: collapse;
                width: 100%;
                background-color: #fff;
                font-size: 14px;
                margin-bottom: 45px;
                line-height: 1.5em;
                th,td{
                    text-align: left;
                    white-space: nowrap;
                    color: #909399;
                    font-weight: 400;
                    border-bottom: 1px solid #dcdfe6;
                    padding: 15px;
                    max-width: 250px;
                }
                td{
                    color: #606266;
                    white-space: normal;
                }
            }
        }
    }
}
</style>



