<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Upload 上传</h2>
            <p slot="title" >通过点击上传文件</p>
        </Viwe>
        <Viwe :explain="explain">
            <h3 slot="name" >点击上传</h3>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <sh-update 
                          :before-upload="fun1"
                          v-model="fileData"
                          >
                          <ShButton size="small" type="primary">上传文件</ShButton>{{ filename }}
                        </sh-update>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description tip">
                                    <p>通过 slot 你可以传入自定义的上传按钮类型和文字提示。可通过设置limit和on-exceed来限制上传文件的个数和定义超出限制时的行为。可通过设置before-remove来阻止文件移除操作。</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;sh-update 
  :before-upload=&quot;fun1&quot;
  v-model=&quot;fileData&quot;
  &gt;
  &lt;ShButton size=&quot;small&quot; type=&quot;primary&quot;&gt;上传文件&lt;/ShButton&gt;{{ filename }}
&lt;/sh-update&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive" class="sh-icon-caret-top"></i>
                        <span @click="isActive = !isActive">{{ isActive?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'Update',
  data(){
    return {
        isActive: false,
        dialogVisible: false,
        filename: '',
        fileData: [],
        explain: [
            {
                "name": "Update Attributes",
                "title":['参数','说明','类型','可选值','默认值'],
                "value":[
                    ['v-model','上传文件数据对象数组','array','—','[]'],
                    ['accept','接受上传的文件类型（thumbnail-mode 模式下此参数无效）','string','—','—'],
                    ['limit','最大允许上传个数','number','—','—'],
                    ['file-list','上传的文件列表, 例如: [file,file,...]','array','—','[]'],
                    ['on-exceed','文件超出个数限制时的钩子','function(files,fileList)','—','-'],
                    ['before-upload','上传文件之前的钩子，参数为上传的文件','function(file)','—','—']
                ],
            }
        ]
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
    fun1(flie){
        this.filename = flie.name
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
</style>



