<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Checkbox 多选框</h2>
            <p slot="title" >一组备选项中进行多选。</p>
        </Viwe>
        <Viwe>
            <h3 slot="name" >基础用法</h3>
            <p slot="title" >单独使用可以表示两种状态之间的切换，写在标签中的内容为 checkbox 按钮后的介绍。</p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <template>
                            <sh-checkbox v-model="checked">备选项1</sh-checkbox>
                            <sh-checkbox v-model="checked">备选项2</sh-checkbox>
                        </template>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>在el-checkbox元素中定义v-model绑定变量，单一的checkbox中，默认绑定变量的值会是Boolean，选中为true。</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;template&gt;
  &lt;!-- &#x60;checked&#x60; 为 true 或 false --&gt;
  &lt;sh-checkbox v-model=&quot;checked&quot;&gt;备选项&lt;/sh-checkbox&gt;
&lt;/template&gt;
&lt;script&gt;
  export default {
    data() {
      return {
        checked: true
      };
    }
  };
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive" class="sh-icon-caret-top"></i>
                        <span @click="isActive = !isActive">{{ isActive?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
        <Viwe>
            <h3 slot="name" >禁用状态</h3>
            <p slot="title" >多选框不可用状态。</p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <template>
                            <sh-checkbox v-model="checked1" disabled>备选项1</sh-checkbox>
                            <sh-checkbox v-model="checked2" disabled>备选项</sh-checkbox>
                        </template>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive1">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>设置disabled属性即可。</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;template&gt;
  &lt;sh-checkbox v-model=&quot;checked1&quot; disabled&gt;备选项1&lt;/sh-checkbox&gt;
  &lt;sh-checkbox v-model=&quot;checked2&quot; disabled&gt;备选项&lt;/sh-checkbox&gt;
&lt;/template&gt;
&lt;script&gt;
  export default {
    data() {
      return {
        checked1: false,
        checked2: true
      };
    }
  };
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive1" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive1" class="sh-icon-caret-top"></i>
                        <span @click="isActive1 = !isActive1">{{ isActive1?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'Checkbox',
  data(){
    return {
        isActive: false,
        isActive1: false,
        checked: true,
        checked1: true,
        checked2: true
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
</style>



