<template>
  <div class="MianRigth">
      <Layoute v-if="this.$store.state.indexID == 12"></Layoute>
      <Container v-else-if="this.$store.state.indexID == 13"></Container>
      <Scrollbar v-else-if="this.$store.state.indexID == 77"></Scrollbar>
      <Color v-else-if="this.$store.state.indexID == 14"></Color>
      <Dialog v-else-if="this.$store.state.indexID == 63"></Dialog>
      <Button v-else-if="this.$store.state.indexID == 18"></Button>
      <ImageOne v-else-if="this.$store.state.indexID == 73"></ImageOne>
      <Input v-else-if="this.$store.state.indexID == 23"></Input>
      <SwitchOne v-else-if="this.$store.state.indexID == 27"></SwitchOne>
      <Link v-else-if="this.$store.state.indexID == 19"></Link>
      <Radio v-else-if="this.$store.state.indexID == 21"></Radio>
      <Checkbox v-else-if="this.$store.state.indexID == 22"></Checkbox>
      <Form v-else-if="this.$store.state.indexID == 36"></Form>
      <Update v-else-if="this.$store.state.indexID == 32"></Update>
      <Color v-else ></Color>
  </div>
</template>

<script>
import Layoute from './tab/Layout'
import Container from './tab/Container'
import Color from './tab/Color'
import Scrollbar from './tab/Scrollbar'
import Dialog from './tab/Dialog'
import Button from './tab/Button'
import ImageOne from './tab/Image'
import Input from './tab/Input'
import SwitchOne from './tab/Switch'
import Link from './tab/Link'
import Radio from './tab/Radio'
import Checkbox from './tab/Checkbox'
import Form from './tab/Form'
import Update from './tab/Update'
export default {
  name: 'MianRigth',
  data(){
    return {
        id:0,
    }
  },
  components: {
      Layoute,
      Container,
      Color,
      Scrollbar,
      Dialog,
      Button,
      ImageOne,
      Input,
      SwitchOne,
      Link,
      Radio,
      Checkbox,
      Form,
      Update
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
</style>



