<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Link 文字链接</h2>
            <p slot="title" >文字超链接。</p>
        </Viwe>
        <Viwe>
            <h3 slot="name" >基础用法</h3>
            <p slot="title" >基础的文字链接用法。</p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <div>
                            <sh-link href="https://shangjiahang.github.io/shui/" target="_blank">默认链接</sh-link>
