                            <sh-link type="success">成功链接</sh-link>
                            <sh-link type="warning">警告链接</sh-link>
                            <sh-link type="danger">危险链接</sh-link>
                            <sh-link type="info">信息链接</sh-link>
                        </div>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;div&gt;
    &lt;sh-link href=&quot;https://shangjiahang.github.io/shui/&quot; target=&quot;_blank&quot;&gt;默认链接&lt;/sh-link&gt;
    &lt;sh-link type=&quot;primary&quot;&gt;主要链接&lt;/sh-link&gt;
    &lt;sh-link type=&quot;success&quot;&gt;成功链接&lt;/sh-link&gt;
    &lt;sh-link type=&quot;warning&quot;&gt;警告链接&lt;/sh-link&gt;
    &lt;sh-link type=&quot;danger&quot;&gt;危险链接&lt;/sh-link&gt;
    &lt;sh-link type=&quot;info&quot;&gt;信息链接&lt;/sh-link&gt;
&lt;/div&gt;
                                        </code>
                                    </pre>
                                </div>
