                            <div class="collapse-wrap">
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;div&gt;
    &lt;sh-link disabled&gt;默认链接&lt;/sh-link&gt;
    &lt;sh-link type=&quot;primary&quot; disabled&gt;主要链接&lt;/sh-link&gt;
    &lt;sh-link type=&quot;success&quot; disabled&gt;成功链接&lt;/sh-link&gt;
    &lt;sh-link type=&quot;warning&quot; disabled&gt;警告链接&lt;/sh-link&gt;
    &lt;sh-link type=&quot;danger&quot; disabled&gt;危险链接&lt;/sh-link&gt;
    &lt;sh-link type=&quot;info&quot; disabled&gt;信息链接&lt;/sh-link&gt;
&lt;/div&gt;
