                        <span @click="isActive1 = !isActive1">{{ isActive1?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
        <Viwe>
            <h3 slot="name" >下划线</h3>
            <p slot="title" >文字链接下划线。</p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <div>
                            <sh-link href="https://shangjiahang.github.io/shui/" target="_blank" :underline="false">无下划线</sh-link>
                            <sh-link >有下划线</sh-link>
                        </div>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive2">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;div&gt;
    &lt;sh-link href=&quot;https://shangjiahang.github.io/shui/&quot; target=&quot;_blank&quot; :underline=&quot;false&quot;&gt;无下划线&lt;/sh-link&gt;
    &lt;sh-link &gt;有下划线&lt;/sh-link&gt;
&lt;/div&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive2" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive2" class="sh-icon-caret-top"></i>
                        <span @click="isActive2 = !isActive2">{{ isActive2?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
        <Viwe>
            <h3 slot="name" >图标</h3>
            <p slot="title" >带图标的文字链接可增强辨识度。</p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <div>
                            <sh-link icon="sh-icon-edit">编辑</sh-link>
                            <sh-link>查看<i class="sh-icon-view sh-icon--right"></i> </sh-link>
                        </div>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive3">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;div&gt;
    &lt;sh-link icon=&quot;sh-icon-edit&quot;&gt;编辑&lt;/sh-link&gt;
    &lt;sh-link&gt;查看&lt;i class=&quot;sh-icon-view sh-icon--right&quot;&gt;&lt;/i&gt; &lt;/sh-link&gt;
&lt;/div&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive3" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive3" class="sh-icon-caret-top"></i>
                        <span @click="isActive3 = !isActive3">{{ isActive3?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'Link',
  data(){
    return {
        isActive: false,
        isActive1: false,
        isActive2: false,
        isActive3: false,
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
</style>



