<template>
  <label
    class="sh-radio-button"
    :class="[
      size ? 'sh-radio-button--' + size : '',
      { 'is-active': value === label },
      { 'is-disabled': isDisabled },
      { 'is-focus': focus }
    ]"
    role="radio"
    :aria-checked="value === label"
    :aria-disabled="isDisabled"
    :tabindex="tabIndex"
    @keydown.space.stop.prevent="value = isDisabled ? value : label"
  >
    <input
      class="sh-radio-button__orig-radio"
      :value="label"
      type="radio"
      v-model="value"
      :name="name"
      @change="handleChange"
      :disabled="isDisabled"
      tabindex="-1"
      @focus="focus = true"
      @blur="focus = false"
    >
    <span
      class="sh-radio-button__inner"
      :style="value === label ? activeStyle : null"
      @keydown.stop>
      <slot></slot>
      <template v-if="!$slots.default">{{label}}</template>
    </span>
  </label>
</template>
<script>
  import Emitter from './js/utils/emitter';

  export default {
    name: 'ShRadioButton',

    mixins: [Emitter],

    inject: {
      elForm: {
        default: ''
      },
      elFormItem: {
        default: ''
      }
    },

    props: {
      label: {},
      disabled: Boolean,
      name: String
    },
    data() {
      return {
        focus: false
      };
    },
    computed: {
      value: {
        get() {
          return this._radioGroup.value;
        },
        set(value) {
          this._radioGroup.$emit('input', value);
        }
      },
      _radioGroup() {
        let parent = this.$parent;
        while (parent) {
          if (parent.$options.componentName !== 'ShRadioGroup') {
            parent = parent.$parent;
          } else {
            return parent;
          }
        }
        return false;
      },
      activeStyle() {
        return {
          backgroundColor: this._radioGroup.fill || '',
          borderColor: this._radioGroup.fill || '',
          boxShadow: this._radioGroup.fill ? `-1px 0 0 0 ${this._radioGroup.fill}` : '',
          color: this._radioGroup.textColor || ''
        };
      },
      _elFormItemSize() {
        return (this.elFormItem || {}).elFormItemSize;
      },
      size() {
        return this._radioGroup.radioGroupSize || this._elFormItemSize || (this.$ELEMENT || {}).size;
      },
      isDisabled() {
        return this.disabled || this._radioGroup.disabled || (this.elForm || {}).disabled;
      },
      tabIndex() {
        return (this.isDisabled || (this._radioGroup && this.value !== this.label)) ? -1 : 0;
      }
    },

    methods: {
      handleChange() {
        this.$nextTick(() => {
          this.dispatch('ShRadioGroup', 'handleChange', this.value);
        });
      }
    }
  };
</script>

<style lang="scss" scoped>
@charset "UTF-8";
.sh-radio-button,.sh-radio-button__inner {
	display:inline-block;
	position:relative;
	outline:0
}
.sh-radio-button__inner {
	line-height:1;
	white-space:nowrap;
	vertical-align:middle;
	background:#FFF;
	border:1px solid #DCDFE6;
	font-weight:500;
	border-left:0;
	color:#606266;
	-webkit-appearance:none;
	text-align:center;
	-webkit-box-sizing:border-box;
	box-sizing:border-box;
	margin:0;
	cursor:pointer;
	-webkit-transition:all .3s cubic-bezier(.645,.045,.355,1);
	transition:all .3s cubic-bezier(.645,.045,.355,1);
	padding:12px 20px;
	font-size:14px;
	border-radius:0
}
.sh-radio-button__inner.is-round {
	padding:12px 20px
}
.sh-radio-button__inner:hover {
	color:#409EFF
}
.sh-radio-button__inner [class*=sh-icon-] {
	line-height:.9
}
.sh-radio-button__inner [class*=sh-icon-]+span {
	margin-left:5px
}
.sh-radio-button:first-child .sh-radio-button__inner {
	border-left:1px solid #DCDFE6;
	border-radius:4px 0 0 4px;
	-webkit-box-shadow:none!important;
	box-shadow:none!important
}
.sh-radio-button__orig-radio {
	opacity:0;
	outline:0;
	position:absolute;
	z-index:-1
}
.sh-radio-button__orig-radio:checked+.sh-radio-button__inner {
	color:#FFF;
	background-color:#409EFF;
	border-color:#409EFF;
	-webkit-box-shadow:-1px 0 0 0 #409EFF;
	box-shadow:-1px 0 0 0 #409EFF
}
.sh-radio-button__orig-radio:disabled+.sh-radio-button__inner {
	color:#C0C4CC;
	cursor:not-allowed;
	background-image:none;
	background-color:#FFF;
	border-color:#EBEEF5;
	-webkit-box-shadow:none;
	box-shadow:none
}
.sh-radio-button__orig-radio:disabled:checked+.sh-radio-button__inner {
	background-color:#F2F6FC
}
.sh-radio-button:last-child .sh-radio-button__inner {
	border-radius:0 4px 4px 0
}
.sh-radio-button:first-child:last-child .sh-radio-button__inner {
	border-radius:4px
}
.sh-radio-button--medium .sh-radio-button__inner {
	padding:10px 20px;
	font-size:14px;
	border-radius:0
}
.sh-radio-button--medium .sh-radio-button__inner.is-round {
	padding:10px 20px
}
.sh-radio-button--small .sh-radio-button__inner {
	padding:9px 15px;
	font-size:12px;
	border-radius:0
}
.sh-radio-button--small .sh-radio-button__inner.is-round {
	padding:9px 15px
}
.sh-radio-button--mini .sh-radio-button__inner {
	padding:7px 15px;
	font-size:12px;
	border-radius:0
}
.sh-radio-button--mini .sh-radio-button__inner.is-round {
	padding:7px 15px
}
.sh-radio-button:focus:not(.is-focus):not(:active):not(.is-disabled) {
	-webkit-box-shadow:0 0 2px 2px #409EFF;
	box-shadow:0 0 2px 2px #409EFF
}
</style>


