<template>
    <div class="Layout">
        <Viwe>
            <h2 slot="name" >Radio 单选框</h2>
            <p slot="title" >在一组备选项中进行单选。</p>
        </Viwe>
        <Viwe>
            <h3 slot="name" >基础用法</h3>
            <p slot="title" >基由于选项默认可见，不宜过多，若选项过多，建议使用 Select 选择器。</p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <template>
                            <sh-radio v-model="radio" label="1">备选项</sh-radio>
                            <sh-radio v-model="radio" label="2">备选项</sh-radio>
                        </template>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>要使用 Radio 组件，只需要设置v-model绑定变量，选中意味着变量的值为相应 Radio label属性的值，label可以是String、Number或Boolean。</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;template&gt;
  &lt;sh-radio v-model=&quot;radio&quot; label=&quot;1&quot;&gt;备选项&lt;/sh-radio&gt;
  &lt;sh-radio v-model=&quot;radio&quot; label=&quot;2&quot;&gt;备选项&lt;/sh-radio&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data () {
      return {
        radio: &#x27;1&#x27;
      };
    }
  }
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive" class="sh-icon-caret-top"></i>
                        <span @click="isActive = !isActive">{{ isActive?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
        <Viwe>
            <h3 slot="name" >禁用状态</h3>
            <p slot="title" >单选框不可用的状态。</p>
            <div slot="content">
                <div class="demo-block" >
                    <div class="source">
                        <template>
                            <sh-radio disabled v-model="radio" label="禁用">备选项</sh-radio>
                            <sh-radio disabled v-model="radio" label="选中且禁用">备选项</sh-radio>
                        </template>
                    </div>
                    <collapse-transition>
                        <div class="meta" v-show="isActive1">
                            <!-- @slot default -->
                            <div class="collapse-wrap">
                                <div class="description">
                                    <p>只要在el-radio元素中设置disabled属性即可，它接受一个Boolean，true为禁用。</p>
                                </div>
                                <div class="highlight">
                                    <pre>
                                        <code>
&lt;template&gt;
  &lt;sh-radio disabled v-model=&quot;radio&quot; label=&quot;禁用&quot;&gt;备选项&lt;/sh-radio&gt;
  &lt;sh-radio disabled v-model=&quot;radio&quot; label=&quot;选中且禁用&quot;&gt;备选项&lt;/sh-radio&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data () {
      return {
        radio: &#x27;选中且禁用&#x27;
      };
    }
  }
&lt;/script&gt;
                                        </code>
                                    </pre>
                                </div>
                            </div>
                        </div>
                    </collapse-transition>
                    <div class="demo-block-control">
                        <i v-show="!isActive1" class="sh-icon-caret-bottom"></i>
                        <i v-show="isActive1" class="sh-icon-caret-top"></i>
                        <span @click="isActive1 = !isActive1">{{ isActive1?'隐藏代码':'显示代码' }}</span>
                    </div>
                </div>
            </div>
        </Viwe>
    </div>
</template>

<script>
import Viwe from '../untils/viwe'
import collapseTransition from "../../../packages/js/utils/animation"
export default {
  name: 'Radio',
  data(){
    return {
        isActive: false,
        isActive1: false,
        radio: '1',
        resource: ''
    }
  },
  components: {
      Viwe,
      collapseTransition
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mianrigth.scss';
</style>



