<template>
  <label
    class="sh-radio"
    :class="[
      border && radioSize ? 'sh-radio--' + radioSize : '',
      { 'is-disabled': isDisabled },
      { 'is-focus': focus },
      { 'is-bordered': border },
      { 'is-checked': model === label }
    ]"
    role="radio"
    :aria-checked="model === label"
    :aria-disabled="isDisabled"
    :tabindex="tabIndex"
    @keydown.space.stop.prevent="model = isDisabled ? model : label"
  >
    <span class="sh-radio__input"
      :class="{
        'is-disabled': isDisabled,
        'is-checked': model === label
      }"
    >
      <span class="sh-radio__inner"></span>
      <input
        ref="radio"
        class="sh-radio__original"
        :value="label"
        type="radio"
        aria-hidden="true"
        v-model="model"
        @focus="focus = true"
        @blur="focus = false"
        @change="handleChange"
        :name="name"
        :disabled="isDisabled"
        tabindex="-1"
      >
    </span>
    <span class="sh-radio__label" @keydown.stop>
      <slot></slot>
      <template v-if="!$slots.default">{{label}}</template>
    </span>
  </label>
</template>
<script>
  import Emitter from './js/utils/emitter';

  export default {
    name: 'ShRadio',

    mixins: [Emitter],

    inject: {
      elForm: {
        default: ''
      },

      elFormItem: {
        default: ''
      }
    },

    componentName: 'ShRadio',

    props: {
      value: {},
      label: {},
      disabled: Boolean,
      name: String,
      border: Boolean,
      size: String
    },

    data() {
      return {
        focus: false
      };
    },
    computed: {
      isGroup() {
        let parent = this.$parent;
        while (parent) {
          if (parent.$options.componentName !== 'ShRadioGroup') {
            parent = parent.$parent;
          } else {
            this._radioGroup = parent;
            return true;
          }
        }
        return false;
      },
      model: {
        get() {
          return this.isGroup ? this._radioGroup.value : this.value;
        },
        set(val) {
          if (this.isGroup) {
            this.dispatch('ShRadioGroup', 'input', [val]);
          } else {
            this.$emit('input', val);
          }
          this.$refs.radio && (this.$refs.radio.checked = this.model === this.label);
        }
      },
      _elFormItemSize() {
        return (this.elFormItem || {}).elFormItemSize;
      },
      radioSize() {
        const temRadioSize = this.size || this._elFormItemSize || (this.$ELEMENT || {}).size;
        return this.isGroup
          ? this._radioGroup.radioGroupSize || temRadioSize
          : temRadioSize;
      },
      isDisabled() {
        return this.isGroup
          ? this._radioGroup.disabled || this.disabled || (this.elForm || {}).disabled
          : this.disabled || (this.elForm || {}).disabled;
      },
      tabIndex() {
        return (this.isDisabled || (this.isGroup && this.model !== this.label)) ? -1 : 0;
      }
    },

    methods: {
      handleChange() {
        this.$nextTick(() => {
          this.$emit('change', this.model);
          this.isGroup && this.dispatch('ShRadioGroup', 'handleChange', this.model);
        });
      }
    }
  };
</script>

<style lang="scss" scoped>
@charset "UTF-8";
.sh-radio,.sh-radio--medium.is-bordered .sh-radio__label {
	font-size:14px
}
.sh-radio,.sh-radio__input {
	white-space:nowrap;
	line-height:1;
	outline:0
}
.sh-radio,.sh-radio__inner,.sh-radio__input {
	position:relative;
	display:inline-block
}
.sh-radio {
	color:#606266;
	font-weight:500;
	cursor:pointer;
	margin-right:30px;
	-moz-user-select:none;
	-webkit-user-select:none;
	-ms-user-select:none
}
.sh-radio.is-bordered {
	padding:12px 20px 0 10px;
	border-radius:4px;
	border:1px solid #DCDFE6;
	-webkit-box-sizing:border-box;
	box-sizing:border-box;
	height:40px
}
.sh-radio.is-bordered.is-checked {
	border-color:#409EFF
}
.sh-radio.is-bordered.is-disabled {
	cursor:not-allowed;
	border-color:#EBEEF5
}
.sh-radio__input.is-disabled .sh-radio__inner,.sh-radio__input.is-disabled.is-checked .sh-radio__inner {
	background-color:#F5F7FA;
	border-color:#E4E7ED
}
.sh-radio.is-bordered+.sh-radio.is-bordered {
	margin-left:10px
}
.sh-radio--medium.is-bordered {
	padding:10px 20px 0 10px;
	border-radius:4px;
	height:36px
}
.sh-radio--mini.is-bordered .sh-radio__label,.sh-radio--small.is-bordered .sh-radio__label {
	font-size:12px
}
.sh-radio--medium.is-bordered .sh-radio__inner {
	height:14px;
	width:14px
}
.sh-radio--small.is-bordered {
	padding:8px 15px 0 10px;
	border-radius:3px;
	height:32px
}
.sh-radio--small.is-bordered .sh-radio__inner {
	height:12px;
	width:12px
}
.sh-radio--mini.is-bordered {
	padding:6px 15px 0 10px;
	border-radius:3px;
	height:28px
}
.sh-radio--mini.is-bordered .sh-radio__inner {
	height:12px;
	width:12px
}
.sh-radio:last-child {
	margin-right:0
}
.sh-radio__input {
	cursor:pointer;
	vertical-align:middle
}
.sh-radio__input.is-disabled .sh-radio__inner {
	cursor:not-allowed
}
.sh-radio__input.is-disabled .sh-radio__inner::after {
	cursor:not-allowed;
	background-color:#F5F7FA
}
.sh-radio__input.is-disabled .sh-radio__inner+.sh-radio__label {
	cursor:not-allowed
}
.sh-radio__input.is-disabled.is-checked .sh-radio__inner::after {
	background-color:#C0C4CC
}
.sh-radio__input.is-disabled+span.sh-radio__label {
	color:#C0C4CC;
	cursor:not-allowed
}
.sh-radio__input.is-checked .sh-radio__inner {
	border-color:#409EFF;
	background:#409EFF
}
.sh-radio__input.is-checked .sh-radio__inner::after {
	-webkit-transform:translate(-50%,-50%) scale(1);
	transform:translate(-50%,-50%) scale(1)
}
.sh-radio__input.is-checked+.sh-radio__label {
	color:#409EFF
}
.sh-radio__input.is-focus .sh-radio__inner {
	border-color:#409EFF
}
.sh-radio__inner {
	border:1px solid #DCDFE6;
	border-radius:100%;
	width:14px;
	height:14px;
	background-color:#FFF;
	cursor:pointer;
	-webkit-box-sizing:border-box;
	box-sizing:border-box
}
.sh-radio__inner:hover {
	border-color:#409EFF
}
.sh-radio__inner::after {
	width:4px;
	height:4px;
	border-radius:100%;
	background-color:#FFF;
	content:"";
	position:absolute;
	left:50%;
	top:50%;
	-webkit-transform:translate(-50%,-50%) scale(0);
	transform:translate(-50%,-50%) scale(0);
	-webkit-transition:-webkit-transform .15s ease-in;
	transition:-webkit-transform .15s ease-in;
	transition:transform .15s ease-in;
	transition:transform .15s ease-in,-webkit-transform .15s ease-in
}
.sh-radio__original {
	opacity:0;
	outline:0;
	position:absolute;
	z-index:-1;
	top:0;
	left:0;
	right:0;
	bottom:0;
	margin:0
}
.sh-radio:focus:not(.is-focus):not(:active):not(.is-disabled) .sh-radio__inner {
	-webkit-box-shadow:0 0 2px 2px #409EFF;
	box-shadow:0 0 2px 2px #409EFF
}
.sh-radio__label {
	font-size:14px;
	padding-left:10px
}
</style>



