<template>
    <div class="sh-upload">
      <input type="file" class="sh-upload__input" ref="file1" name="filename" @change="handleChange($event)" :accept="accept">
      <div @click="updatefile">
        <slot v-if=""></slot>
        <template v-if="!$slots.default">
          <button>updateFile</button>
        </template>
      </div>
    </div>
</template>

<script>

export default {
  name: 'ShUpdate',
  props:{
    accept:{
      type: String,
      default: ''
    },
    value: Array,//返回上传文件数据对象数组
    beforeUpload: Function,//文件上传选择完成后执行函数
    limit: Number,//限制上传的文件个数
    fileList: Array,//已经存在的文件
    onExceed: Function,//文件超出的回调钩子函数
  },
  methods: {
    handleChange(ev){
      const files = ev.target.files;
      if (!files) return;
      this.uploadFiles(files);
    },
    uploadFiles(files) {
      //判断文件上传的个数是否正确
      if (files.length > this.limit) {
        this.onExceed && this.onExceed(files, this.fileList);
        return;
      }
      //回调文件上传完成时方法
      if (this.beforeUpload) {  //存在上传完后回调
        for(var i=0;i<files.length;i++){
          this.beforeUpload(files[i]);
        }
      }
      //返回上传文件对象
      let postFiles = Array.prototype.slice.call(files);
      if(this.value){
        this.$emit('input',postFiles)
      }
    },
    updatefile(){
      this.$refs.file1.click()
    }
  }
}
</script>

<style lang="scss" scoped>
@charset "UTF-8";
.sh-upload {
	display:inline-block;
	text-align:center;
	cursor:pointer;
	outline:0;
  >.sh-upload__input {
    display:none
  }
}
</style>



