<template>
    <div class="sh-form">
        <slot></slot>
    </div>
</template>

<script>

export default {
  name: 'ShForm',
  props:{
      model:{
          type: Object,
          required: true
      },
      labelWidth:{
          type: String,
          default: '80px'
      }
  },
  provide (){
      return {
          Form: this
      }
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>

</style>



