<template>
  <label
    class="sh-checkbox-button"
      :class="[
        size ? 'sh-checkbox-button--' + size : '',
        { 'is-disabled': isDisabled },
        { 'is-checked': isChecked },
        { 'is-focus': focus },
      ]"
    role="checkbox"
    :aria-checked="isChecked"
    :aria-disabled="isDisabled"
    >
    <input
      v-if="trueLabel || falseLabel"
      class="sh-checkbox-button__original"
      type="checkbox"
      :name="name"
      :disabled="isDisabled"
      :true-value="trueLabel"
      :false-value="falseLabel"
      v-model="model"
      @change="handleChange"
      @focus="focus = true"
      @blur="focus = false">
    <input
      v-else
      class="sh-checkbox-button__original"
      type="checkbox"
      :name="name"
      :disabled="isDisabled"
      :value="label"
      v-model="model"
      @change="handleChange"
      @focus="focus = true"
      @blur="focus = false">

    <span class="sh-checkbox-button__inner"
      v-if="$slots.default || label"
      :style="isChecked ? activeStyle : null">
      <slot>{{label}}</slot>
    </span>

  </label>
</template>
<script>
  import Emitter from './js/utils/emitter';

  export default {
    name: 'ShCheckboxButton',

    mixins: [Emitter],

    inject: {
      elForm: {
        default: ''
      },
      elFormItem: {
        default: ''
      }
    },

    data() {
      return {
        selfModel: false,
        focus: false,
        isLimitExceeded: false
      };
    },

    props: {
      value: {},
      label: {},
      disabled: Boolean,
      checked: Boolean,
      name: String,
      trueLabel: [String, Number],
      falseLabel: [String, Number]
    },
    computed: {
      model: {
        get() {
          return this._checkboxGroup
            ? this.store : this.value !== undefined
              ? this.value : this.selfModel;
        },

        set(val) {
          if (this._checkboxGroup) {
            this.isLimitExceeded = false;
            (this._checkboxGroup.min !== undefined &&
              val.length < this._checkboxGroup.min &&
              (this.isLimitExceeded = true));

            (this._checkboxGroup.max !== undefined &&
              val.length > this._checkboxGroup.max &&
              (this.isLimitExceeded = true));

            this.isLimitExceeded === false &&
            this.dispatch('ElCheckboxGroup', 'input', [val]);
          } else if (this.value !== undefined) {
            this.$emit('input', val);
          } else {
            this.selfModel = val;
          }
        }
      },

      isChecked() {
        if ({}.toString.call(this.model) === '[object Boolean]') {
          return this.model;
        } else if (Array.isArray(this.model)) {
          return this.model.indexOf(this.label) > -1;
        } else if (this.model !== null && this.model !== undefined) {
          return this.model === this.trueLabel;
        }
      },

      _checkboxGroup() {
        let parent = this.$parent;
        while (parent) {
          if (parent.$options.componentName !== 'ShCheckboxGroup') {
            parent = parent.$parent;
          } else {
            return parent;
          }
        }
        return false;
      },

      store() {
        return this._checkboxGroup ? this._checkboxGroup.value : this.value;
      },

      activeStyle() {
        return {
          backgroundColor: this._checkboxGroup.fill || '',
          borderColor: this._checkboxGroup.fill || '',
          color: this._checkboxGroup.textColor || '',
          'box-shadow': '-1px 0 0 0 ' + this._checkboxGroup.fill

        };
      },

      _elFormItemSize() {
        return (this.elFormItem || {}).elFormItemSize;
      },

      size() {
        return this._checkboxGroup.checkboxGroupSize || this._elFormItemSize || (this.$ELEMENT || {}).size;
      },

      /* used to make the isDisabled judgment under max/min props */
      isLimitDisabled() {
        const { max, min } = this._checkboxGroup;
        return !!(max || min) &&
          (this.model.length >= max && !this.isChecked) ||
          (this.model.length <= min && this.isChecked);
      },

      isDisabled() {
        return this._checkboxGroup
          ? this._checkboxGroup.disabled || this.disabled || (this.elForm || {}).disabled || this.isLimitDisabled
          : this.disabled || (this.elForm || {}).disabled;
      }
    },
    methods: {
      addToStore() {
        if (
          Array.isArray(this.model) &&
          this.model.indexOf(this.label) === -1
        ) {
          this.model.push(this.label);
        } else {
          this.model = this.trueLabel || true;
        }
      },
      handleChange(ev) {
        if (this.isLimitExceeded) return;
        let value;
        if (ev.target.checked) {
          value = this.trueLabel === undefined ? true : this.trueLabel;
        } else {
          value = this.falseLabel === undefined ? false : this.falseLabel;
        }
        this.$emit('change', value, ev);
        this.$nextTick(() => {
          if (this._checkboxGroup) {
            this.dispatch('ShCheckboxGroup', 'change', [this._checkboxGroup.value]);
          }
        });
      }
    },

    created() {
      this.checked && this.addToStore();
    }
  };
</script>

<style lang="scss" scoped>
@charset "UTF-8";
.sh-checkbox-button,.sh-checkbox-button__inner {
	display:inline-block;
	position:relative
}
.sh-checkbox__label {
	display:inline-block;
	padding-left:10px;
	line-height:19px;
	font-size:14px
}
.sh-checkbox:last-of-type {
	margin-right:0
}
.sh-checkbox-button__inner {
	line-height:1;
	font-weight:500;
	cursor:pointer;
	background:#FFF;
	border:1px solid #DCDFE6;
	border-left:0;
	color:#606266;
	-webkit-appearance:none;
	text-align:center;
	-webkit-box-sizing:border-box;
	box-sizing:border-box;
	margin:0;
	-webkit-transition:all .3s cubic-bezier(.645,.045,.355,1);
	transition:all .3s cubic-bezier(.645,.045,.355,1);
	-moz-user-select:none;
	-webkit-user-select:none;
	-ms-user-select:none;
	padding:12px 20px;
	font-size:14px;
	border-radius:0
}
.sh-checkbox-button__inner.is-round {
	padding:12px 20px
}
.sh-checkbox-button__inner:hover {
	color:#409EFF
}
.sh-checkbox-button__inner [class*=sh-icon-] {
	line-height:.9
}
.sh-checkbox-button__inner [class*=sh-icon-]+span {
	margin-left:5px
}
.sh-checkbox-button__original {
	opacity:0;
	outline:0;
	position:absolute;
	margin:0;
	z-index:-1
}
.sh-checkbox-button.is-checked .sh-checkbox-button__inner {
	color:#FFF;
	background-color:#409EFF;
	border-color:#409EFF;
	-webkit-box-shadow:-1px 0 0 0 #8cc5ff;
	box-shadow:-1px 0 0 0 #8cc5ff
}
.sh-checkbox-button.is-checked:first-child .sh-checkbox-button__inner {
	border-left-color:#409EFF
}
.sh-checkbox-button.is-disabled .sh-checkbox-button__inner {
	color:#C0C4CC;
	cursor:not-allowed;
	background-image:none;
	background-color:#FFF;
	border-color:#EBEEF5;
	-webkit-box-shadow:none;
	box-shadow:none
}
.sh-checkbox-button.is-disabled:first-child .sh-checkbox-button__inner {
	border-left-color:#EBEEF5
}
.sh-checkbox-button:first-child .sh-checkbox-button__inner {
	border-left:1px solid #DCDFE6;
	border-radius:4px 0 0 4px;
	-webkit-box-shadow:none!important;
	box-shadow:none!important
}
.sh-checkbox-button.is-focus .sh-checkbox-button__inner {
	border-color:#409EFF
}
.sh-checkbox-button:last-child .sh-checkbox-button__inner {
	border-radius:0 4px 4px 0
}
.sh-checkbox-button--medium .sh-checkbox-button__inner {
	padding:10px 20px;
	font-size:14px;
	border-radius:0
}
.sh-checkbox-button--medium .sh-checkbox-button__inner.is-round {
	padding:10px 20px
}
.sh-checkbox-button--small .sh-checkbox-button__inner {
	padding:9px 15px;
	font-size:12px;
	border-radius:0
}
.sh-checkbox-button--small .sh-checkbox-button__inner.is-round {
	padding:9px 15px
}
.sh-checkbox-button--mini .sh-checkbox-button__inner {
	padding:7px 15px;
	font-size:12px;
	border-radius:0
}
.sh-checkbox-button--mini .sh-checkbox-button__inner.is-round {
	padding:7px 15px
}
</style>


